.main__bg img{
    width: 100%;
    height: 100%;
    z-index: 15;
    position: fixed;
    left: 50%;
    top: 50%;
    object-fit: cover;
    transform: translate(-50%,-50%);
}
.main__bg__container{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 25;
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
            z-index: 25;
        }
    .main__bg__container_1{
        animation: main__bg__container_1 10s infinite alternate;
        @keyframes main__bg__container_1 {
            0%{opacity: 1;}
            17%{opacity: 1;}
            34%{opacity: 0;}
            51%{opacity: 0;}
            68%{opacity: 0;}
            84%{opacity: 0;}
            100%{opacity: 0;}
        }
    }        
    .main__bg__container_2{
